.footer-bottom {
  padding-top: 30px;
  padding-bottom: 30px;
  color: #fff;  
  display: flex;
  flex-direction: row;
  background-color: #37517e;
  margin: 0px 105px;
}


 .copyright {
  float: left;
}

 .credits {
  float: right;
  font-size: 13px;
}

 .credits a {
  transition: 0.3s;
}

@media (max-width: 768px) {
   .footer-bottom {
    padding-top: 20px;
    padding-bottom: 20px;
  }

   .copyright,
   .credits {
    text-align: center;
    float: none;
  }

   .credits {
    padding-top: 4px;
  }
}














