*{
  overflow-x: visible;
}
.holder{
  display:flex; 
  flex-direction: column;
  justify-content:center;
  
  
  overflow: visible;
}



#contain {
  width: 100%;
  height: 530px;
  background-color: #1D3557;
  
  display:flex;
  align-items:center;
  padding-bottom: 20px;
  overflow: visible;
}
#contain .containImage{
  position: absolute;
  width: 1440px;
  height: 530px;
  left: 0px;
  top: 0px;
  width: 100%;
  
  mix-blend-mode: multiply;
}

#contain .container {
  
  height: 100%;
  padding: 0px;
  justify-content: space-evenly;
  display: flex;
}

#contain .container .row{
  
  //width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.holder .links{
  display:flex;
  justify-content:start; 
  margin:20px 0px;
  //padding-left: 20px;
}
#contain h3 {
  color: #fff;
  font-weight: 300;
  margin-bottom: 40px;

  
}
#contain h1 {
  margin: 0 0 10px 0;
  font-size: 37px;
  font-weight: 700;
  line-height: 49.22px;
  color: #fff;
}


#contain h2 {
  color: rgba(255, 255, 255, 0.6);
  margin-bottom: 50px;
  font-size: 24px;
}
.watchVideo{
  display: flex;
  align-items: center;
}
.watchVideo .watch-video-text {
  font-size: 16px;
  display: flex;
  align-items: center;
  transition: 0.5s;
  margin: 0px 0 0 5px;
  color: #fff;
  line-height: 1;
}

#contain .btn-watch-video  {
  line-height: 0;
  color: #fff;
  font-size: 36px;
  transition: 0.3s;
  margin-left: 18px;
  display: flex;
  align-items: center;
  &:hover{
    color: #47b2e4;
  }
}

.holder-img{
  display: flex;
  justify-content: center;
  align-content: stretch;
}
#contain .hero-img {
    filter: grayscale(0);
    animation: float 2s ease-in-out infinite alternate-reverse both;   
    height: 500px;
}

@media (max-width: 991px) {
  #contain {
    height: 700px !important;
    text-align: center;
  }
  #contain .containImage{
    height: 700px !important
  }
  #contain .container {
  row-gap: 0px;
}
  #contain .hero-img {
    -webkit-animation: none;
    animation: none;
    
    height: 50vh;
    width: 50vh;
  }

.holder{
  align-items: center;
  height: fit-content;

}
.holder .links{
  margin:20px 0px !important;
}

  
}

@media (max-width: 768px) {
  #contain {
    height: 800px !important;
    text-align: center;
  }
  #contain .containImage{
    height: 800px !important
  }
  #contain .container {
  row-gap: 0px;
}
 #contain .hero-img {
    width: 70%;
  }
  .holder{
    //padding-top: 20px;
  }
  .holder .links{
    margin-bottom: 20px !important;
  }
  #contain h1 {
    font-size: 28px;
    line-height: 36px;
  }

  #contain h2 {
    font-size: 18px;
    line-height: 24px;
    //margin: 30px 0px;
  }

 
}

@media (max-width: 575px) {
    #contain {
      height: 748px !important;
    }
  .holder .links{
    margin-bottom: 30px;
  }  
  #contain .containImage{
    height: 748px !important
  }
  #contain .hero-img img {
    width: 80%;
  }

  #contain .btn-get-started {
    font-size: 16px;
    padding: 10px 24px ;
  }
}

@-webkit-keyframes float {
  0% {
    transform: translateY(10px);
  }

  100% {
    transform: translateY(-10px);
  }
}

@keyframes float {
  0% {
    transform: translateY(10px);
  }

  100% {
    transform: translateY(-10px);
  }
}
